.modal__backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.75);

  .modal__content2 {
    // text-align: center;
    display: flex;
    justify-content: center;
    align-self: center;
    position: absolute;
    flex-direction: column;
    top: 56%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 400px;
    min-height: 100px;
    width: 100%;
    padding: 12px;
    background-color: #001838;
    color: #fff;
    border: #2fc5f9 solid 1px;
    border-radius: 15px;
    box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
      0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);

    // @media (max-width: 430px) {
    //   top: 49%;
    // }
    @media (max-width: 600px) {
      width: 330px;
    }
    @media (max-width: 360px) {
      width: 300px;
    }

    .modal__login {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;

      @media (max-width: 600px) {
        font-size: 13px;
      }
      @media (max-width: 360px) {
        font-size: 12px;
      }

      .modal__ok {
        width: 90px;
        height: 23px;
        margin-top: 20px;
        border-radius: 15px;
        border: none;
        background-color: #f3d88e;
        color: #001838;
        cursor: pointer;
      }
    }
  }
}
